body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #282c34;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-layout {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

::-webkit-scrollbar{
	width: 6px;
}

::-webkit-scrollbar-thumb{
  background-color: black;
}

.hero {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin: 5px;
  padding-left: 60px;
  padding-right: 10px;
  z-index: 1;
  text-align: left; }
  .hero:hover {
    background-color: rgba(0, 0, 0, 0.2); }
  .hero:before {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: black;
    opacity: .2;
    content: "";
    z-index: -1; }
  .hero .hero__icon {
    width: 32px;
    height: 32px; }
  .hero .hero__name {
    width: 200px; }
  .hero .hero__cost {
    width: 80px;
    color: #E6E6E6;
    text-align: right; }
    .hero .hero__cost span {
      display: inline-block;
      width: 28px;
      height: 28px;
      background: #E6E6E6;
      border-radius: 28px;
      color: #000;
      text-align: center;
      line-height: 28px; }
    .hero .hero__cost__1 {
      color: #B7B7B7; }
    .hero .hero__cost__2 {
      color: #9498BC; }
    .hero .hero__cost__3 {
      color: #4344F4; }
    .hero .hero__cost__4 {
      color: #FD03FB; }
    .hero .hero__cost__5 {
      color: #F98B00; }
  .hero .hero__species {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    text-align: left;
    width: 200px; }
    .hero .hero__species div:nth-of-type(1) {
      margin-right: 20px; }
  .hero .hero__ability {
    width: 100px; }
  .hero .hero__className {
    width: 200px; }
    .hero .hero__className__Assassin {
      color: #E7D453; }
    .hero .hero__className__Demon {
      color: #6C0793; }
    .hero .hero__className__Druid {
      color: #D86304; }
    .hero .hero__className__Hunter {
      color: #9ABE66; }
    .hero .hero__className__Knight {
      color: #ED8CB7; }
    .hero .hero__className__Mage {
      color: #66C5E1; }
    .hero .hero__className__Mech {
      color: #B57A00; }
    .hero .hero__className__Shaman {
      color: #2036B0; }
    .hero .hero__className__Warlock {
      color: #5C489F; }
    .hero .hero__className__Warrior {
      color: #B38E61; }

.ability img {
  width: 32px;
  height: 32px; }

.icon {
  width: 32px;
  height: 32px; }

.class.Assassin {
  color: #E7D453; }

.class.DemonHunter {
  color: #6C0793; }

.class.Druid {
  color: #D86304; }

.class.Hunter {
  color: #9ABE66; }

.class.Knight {
  color: #ED8CB7; }

.class.Mage {
  color: #66C5E1; }

.class.Mech {
  color: #B57A00; }

.class.Shaman {
  color: #2036B0; }

.class.Warlock {
