.species {
    color: white;

    & p.active {
        opacity: 1;
    }
    & p.inactive {
        opacity: 0.3;
    }
    &.Orc {
        color: #E7032B;
    }
    &.Beast {
        color: #38820E;
    }
    &.Ogre {
        color: #11B7D2;
    }
    &.Undead {
        color: #464646;
    }
    &.Goblin {
        color: #C53716;
    }
    &.Troll {
        color: #8F926B;
    }
    &.Human {
        color: white;
    }
    &.Demon {
        color: #8715CF;
    }
    &.Naga {
        color: #4E48CF;
    }
    &.Elf {
        color: #1076CA;
    }
    &.Element {
        color: #FBF91C;
    }
    &.Satyr {
        color: rgb(138, 42, 202);
    }
    &.Dwarf {
        color: orange;
    }
    &.Dragon {
        color: darkred;
    }
}